import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { reset } from "redux-form";
import Swal from "sweetalert2";
// import { reset } from "redux-form";
import { hideModal, showModal } from "../../../actions/datamaster_action.jsx";
import {
  setBatalPenjualan,
  setNoFakturPenjualan,
} from "../../../actions/penjualan_action.jsx";
import {
  getItem,
  localStoragedecryp,
  localStorageencryp,
  setItem,
} from "../../../components/helpers/function.jsx";
// import { setBatalPenjualan, setNoFakturPenjualan } from "../../../actions/penjualan_action.jsx";
import ModalGlobal from "../../../components/helpers/ModalGlobal.jsx";
import { ToastNotification } from "../../../components/helpers/notification.jsx";
// import { ToastNotification } from "../../../components/helpers/notification.jsx";
// import { ToastNotification } from "../../../components/helpers/notification.jsx";
import {
  Panel,
  PanelBody,
  PanelHeader,
} from "../../../components/panel/panel.jsx";
import { getData, postData, postData2 } from "../../../config/axios.jsx";
import { PageSettings } from "../../../config/page-settings.js";
// import { postData } from "../../../config/axios.jsx";
import HeadBatalPenjualan from "./HeadBatalPenjualan.jsx";
import OtorisasiBatalJual from "./OtorisasiBatalJual.jsx";

const maptostate = (state) => {
  return {
    // no_faktur: state.penjualan.setNoFakturPenjualan,
    setBatalPenjualan: state.penjualan.setBatalPenjualan,
  };
};
class FormBatalPenjualan extends React.Component {
  static contextType = PageSettings;
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      databarang: [],
      isLoadingOtorisasi: false,
      columns: [
        {
          dataField: "no_faktur_jual",
          text: "No Faktur",
          footer: "Total Barang ",
        },
        {
          dataField: "kode_barcode",
          text: "Kode Barcode",
          footer: "",
        },
        {
          dataField: "nama_barang",
          text: "Nama Barang",
          footer: "",
        },
        {
          dataField: "berat",
          text: "Berat",
          formatter: (cell) => {
            return <span>{parseInt(cell).toFixed(1)}</span>;
          },
          footer: (columnData) =>
            parseInt(columnData.reduce((acc, item) => acc + item, 0)).toFixed(
              1
            ),
        },
        // {
        //   dataField: "nama_atribut",
        //   text: "Nama Atribut",
        // },
        // {
        //   dataField: "harga_atribut",
        //   text: "Harga Atribut",
        //   formatter: (cell) => {
        //     return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
        //   },
        //   footer: (columnData) =>
        //     parseInt(
        //       columnData.reduce((acc, item) => acc + item, 0)
        //     ).toLocaleString("kr-KO"),
        // },

        // {
        //   dataField: "berat_atribut",
        //   text: "Berat Atribut",
        //   formatter: (cell) => {
        //     return <span>{parseInt(cell).toFixed(1)}</span>;
        //   },
        //   footer: (columnData) =>
        //     parseInt(columnData.reduce((acc, item) => acc + item, 0)).toFixed(
        //       1
        //     ),
        // },

        {
          dataField: "harga_jual",
          text: "Harga Jual",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO"),
        },
        {
          dataField: "harga_total",
          text: "Harga Total",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO"),
        },
        {
          dataField: "alasan",
          text: "Alasan",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row, index) => {
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    onClick={() => {
                      this.hapusDataBarang(index);
                    }}
                    className="btn btn-danger"
                    type="button"
                  >
                    <i className="fa fa-trash"></i>
                  </button>
                </div>
              </div>
            );
          },
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  hapusDataBarang(id) {
    Swal.fire({
      title: "Anda yakin ingin mengapus barang ini ? ",
      icon: "warning",
      position: "top-center",
      cancelButtonText: "Tidak",
      showCancelButton: true,
      confirmButtonText: "OK",
      showConfirmButton: true,
    }).then((result) => {
      if (result.isConfirmed) {
        let data = getItem("data_barang_batal_jual");
        data.splice(id, 1);
        setItem("data_barang_batal_jual", data);
        this.setState({
          status: "berhasil",
        });
      }
    });
  }
  handleSubmit(data) {
    getData("batalpenjualan/get/by-kode-barcode/" + data.kode_barcode)
      .then((res) => {
        if (res.data.length === 0) {
          ToastNotification("info", "Kode Barcode Yang Anda Cari Tidak Ada");
          this.setState({
            isloadingFaktur: false,
          });
        } else {
          res.data.forEach((item) => {
            let row = {
              alasan: data.alasan,
              kode_barcode: item.kode_barcode,
              no_faktur_group: item.no_faktur_group,
              no_faktur_jual: item.no_faktur_jual,
              nama_barang: item.nama_barang,
              berat: item.berat,
              harga_jual: item.harga_jual,
              harga_total: item.harga_total,
              harga_atribut: item.harga_atribut,
              nama_atribut: item.nama_atribut,
              ongkos: item.ongkos,
            };
            let cek =
              localStoragedecryp("data_barang_batal_jual").length === 0
                ? []
                : JSON.parse(localStoragedecryp("data_barang_batal_jual"));

            if (cek.length === 0) {
              cek.push(row);
              localStorageencryp("data_barang_batal_jual", JSON.stringify(cek));
              this.setState({
                status: "berhasil",
              });
              return false;
            }

            let hasil_cek = cek.findIndex(
              (hasil) => hasil.kode_barcode === item.kode_barcode
            );
            if (hasil_cek === -1) {
              let hasil_cek_faktur = cek.findIndex(
                (hasil) => hasil.no_faktur_group === item.no_faktur_group
              );
              if (hasil_cek_faktur === -1) {
                ToastNotification("info", "No Faktur Tidak Sama");
              } else {
                cek.push(row);
                localStorageencryp(
                  "data_barang_batal_jual",
                  JSON.stringify(cek)
                );
              }
            } else {
              ToastNotification("info", "Kode Barcode Sudah Ada Di Tabel");
            }
            this.setState({
              status: "berhasil",
            });
          });
        }
      })
      .catch((err) => {
        this.setState({
          isloadingFaktur: false,
        });
        ToastNotification("info", "Kode Barcode Yang Anda Cari Tidak Ada");
      });
    // let hasil = {
    //   no_faktur_group: data.no_faktur,
    //   alasan: data.alasan,
    // };
    // localStorage.setItem("no_faktur_batal_jual", JSON.stringify(hasil));
  }
  simpanTransaksiJual(data) {
    this.setState({
      isLoadingOtorisasi: true,
    });
    let data_batal_penjualan = getItem("data_barang_batal_jual");

    let data_barang_jual = [];
    data_batal_penjualan.forEach((item) => {
      let row = {
        no_faktur_group: item.no_faktur_group,
        no_faktur_jual: item.no_faktur_jual,
        alasan: item.alasan,
      };
      data_barang_jual.push(row);
    });

    let hasil = {
      user_id: data.username,
      password: data.password,
      kategori: "BATAL PENJUALAN",
      description: "SIMPAN BATAL PENJUALAN",
      keterangan: data.keterangan.toUpperCase() || "-",
    };
    postData2("authorization", hasil)
      .then((resposn) => {
        ToastNotification("success", "Otorisasi Berhasil");
        this.setState({
          isLoadingOtorisasi: true,
        });
        postData("batalpenjualan/simpan", data_barang_jual)
          .then((res) => {
            this.setState({ isLoadingOtorisasi: false });
            this.props.dispatch(setBatalPenjualan([]));
            this.props.dispatch(setNoFakturPenjualan([]));
            ToastNotification("success", res.data.message);
          })
          .then(() => {
            this.props.dispatch(reset("HeadBatalPenjualan"));
            this.props.dispatch(reset("OtorisasiBatalJual"));
            setItem("data_barang_batal_jual", []);
            this.setState({ isLoadingOtorisasi: false });
            this.props.dispatch(hideModal());
          })
          .catch((err) => {
            this.setState({ isLoadingOtorisasi: false });
            // console.log(err);
            ToastNotification(
              "info",
              err.response === undefined
                ? "Error menyimpan data, Silahkan Cek tanggal transaksi dan koneksi anda"
                : err.response.data
            );
          });
      })
      .catch((err) => {
        ToastNotification("info", "Username atau Password Salah");
        this.setState({
          isLoadingOtorisasi: false,
        });
      });
  }
  simpanBatal() {
    this.props.dispatch(showModal());

    // console.log(data);
  }

  componentDidMount() {
    this.context.handleSetPageSidebar(false);
    this.context.handleSetPageHeader(false);
  }

  render() {
    return (
      <div>
        <Link to="/SubMenuPenjualan">
          <button
            type="button"
            className="btn btn-secondary"
            data-tut="reactour_4"
          >
            <i className="fa fa-angle-left mr-3" aria-hidden="true"></i> Home
          </button>
        </Link>
        <br />
        <br />
        <Panel>
          <PanelHeader>Batal Penjualan</PanelHeader>
          <br />
          <PanelBody>
            <HeadBatalPenjualan
              isLoading={this.state.isLoading}
              onSubmit={(data) => this.handleSubmit(data)}
              data={this.state.databarang}
              columns={this.state.columns}
              simpanBatal={() => this.simpanBatal()}
            />
          </PanelBody>
          <br />
          <ModalGlobal
            size="L"
            title="Otorisasi Pembatalan"
            content={
              <OtorisasiBatalJual
                isLoadingOtorisasi={this.state.isLoadingOtorisasi}
                onSubmit={(data) => this.simpanTransaksiJual(data)}
              />
            }
          />
        </Panel>
      </div>
    );
  }
}

export default connect(maptostate, null)(FormBatalPenjualan);
