/* eslint-disable no-undef */
import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm, reset } from "redux-form";
import { getDataKondisiBarang } from "../../../actions/datamaster_action";
import { ReanderField, ReanderSelect } from "../../../components/helpers/field";
import { ToastNotification } from "../../../components/helpers/notification";
import Tabel from "../../../components/helpers/tabel";
import { getData, postData } from "../../../config/axios";
// import { SkeletonLoading } from "../../../components/helpers/field.jsx";
import ValidasiBaralJual from "../../../Validasi/Penjualan/ValidasiBaralJual";
import {
  setBatalPenjualan,
  setNoFakturPenjualan,
} from "../../../actions/penjualan_action";
// import Loading from 'react-fullscreen-loading';
import { localStoragedecryp, setItem } from "../../../components/helpers/function";

const maptostate = (state) => {
  return {
    no_faktur: state.penjualan.setNoFakturPenjualan,
    setBatalPenjualan: state.penjualan.setBatalPenjualan,
    datakondisi: state.datamaster.getDataKondisiBarang,
  };
};
class HeadBatalPenjualan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      // no_faktur: [],
      isloadingFaktur: false,
      isLoading: false,
      isLoadingBatal: false,
      date: new Date(),
    };
  }

  getBarcode(value) {
    clearTimeout(this.delay)
    this.delay = setTimeout(() => {
    this.setState({ isloadingFaktur: true });
    getData("batalpenjualan/get/by-kode-barcode/" + value)
      .then((res) => {
        if (res.data.length === 0) {
          ToastNotification("info", "Kode Barcode Yang Anda Cari Tidak Ada");
          this.setState({
            isloadingFaktur: false,
          });
        } else {
          console.log(res.data);
          let hasil = [{ no_faktur_group: "Silahkan Pilih", no_faktur_jual : "Silahkan Pilih"}];
          res.data.forEach((item, index) => {
            hasil.push({
              no_faktur_group: item._id.no_faktur_group,
              no_faktur_jual: item._id.no_faktur_jual
            });
            // console.log(item._id.no_faktur_group);
            this.props.change("no_faktur", "Silahkan Pilih");
            this.setState({
              isloadingFaktur: false,
            });
            this.props.dispatch(setNoFakturPenjualan(hasil));
          });
        }
      })
      .catch((err) => {
        this.setState({
          isloadingFaktur: false,
        });
        ToastNotification("info", "Kode Barcode Yang Anda Cari Tidak Ada");
      });
    }, 1500);
  }
  getDetailFaktur() {
    this.setState({ isLoading: true });
    let data = {
      no_faktur_group: this.state.no_faktur_group_selected,
    };
    postData("batalpenjualan/get/by-faktur", data)
      .then((res) => {
        if (res.data.length === 0) {
          ToastNotification("info", "Data Transaksi Tersbut Tidak Ada");
          this.setState({ isLoading: false });
        } else {
          this.props.dispatch(setBatalPenjualan(res.data));
          this.setState({
            isLoading: false,
          });
        }
      })
      .catch((err) => {
        ToastNotification("info", "Data Transaksi Tersbut Tidak Ada");
        this.setState({ isLoading: false });
      });
  }
  componentDidMount() {
    this.props.dispatch(getDataKondisiBarang());
    setTimeout(() => {
      document.getElementById("kode_barcode").focus()
      HideKeyboard.postMessage("");
    }, 100);
  }
  btlPenjualan() {
    this.setState({ isLoadingBatal: true });
    setTimeout(() => {
      this.props.dispatch(reset("HeadBatalPenjualan"));
      this.setState({
        isLoadingBatal: false,
        no_faktur_group_selected: "",
      });
      setItem('data_barang_batal_jual',[])
      this.props.dispatch(setNoFakturPenjualan([]));
      this.props.dispatch(setBatalPenjualan([]));
    }, 500);
  }
  render() {
    
    return (
      <form
        onSubmit={this.props.handleSubmit}
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
        autoComplete="off"
      >
        <div className="row">
          <div className="col-lg-4">
            <Field
              // onChange={(e) => this.getBarcode(e.target.value)}
              id="kode_barcode"
              name="kode_barcode"
              component={ReanderField}
              type="text"
              label="Kode Barcode"
              placeholder="Masukan Kode Barcode"
            />
          </div>
          {/* <div className="col-lg-3">
            <Field
              label="No Faktur"
              name="no_faktur"
              id="no_faktur"
              onChange={(e) => this.setState({ no_faktur_group_selected: e })}
              options={this.props.no_faktur.map((row) => {
                console.log(row);
                let data = {
                  value: row.no_faktur_group,
                  name: row.no_faktur_jual,
                };
                return data || [];
              })}
              placeholder={"Silahkan Pilih No Faktur"}
              component={ReanderSelect}
            />
          </div> */}
          <div className="col-lg-4">
            <Field
              label="Alasan"
              name="alasan"
              options={this.props.datakondisi
                .filter((list) => list.kondisi_barang !== undefined)
                .map((list) => {
                  let data = {
                    value: list.kondisi_barang,
                    name: list.kondisi_barang,
                  };
                  return data;
                })}
              placeholder={"Silahkan Pilih Alasan"}
              component={ReanderSelect}
            />
          </div>
          <div className="col-lg-4">
            <div className="text-right">
              <label>&nbsp;</label>
              <button
                type="submit"
                // onClick={() => this.getDetailFaktur()}
                name="type_btn"
                className="btn btn-primary btn-block"
                disabled={this.state.isLoading}
              >
                {this.state.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Mencari
                  </>
                ) : (
                  "Cari Data"
                )}
              </button>
            </div>
          </div>
          <div className="col-lg-12">
            <Tabel
              keyField="kode_barcode"
              exportCsv={false}
              data={
                localStoragedecryp("data_barang_batal_jual").length === 0
                    ? []
                    : JSON.parse(
                        localStoragedecryp("data_barang_batal_jual")
                      )
              }
              columns={this.props.columns}
              empty={true}
              textEmpty="Data Barang Kosong"
            />
          </div>
          <div className="col-lg-12">
            <label>&nbsp;</label>
            <button
              type="button"
              onClick={()=>this.props.simpanBatal()}
              name="type_btn"
              className="btn btn-primary"
              disabled={this.props.isLoading}
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp;Simpan Batal
                  Penjualan
                </>
              ) : (
                "Simpan Batal Penjualan"
              )}
            </button>
            &nbsp;
            <button
              type="button"
              name="type_btn"
              onClick={() => this.btlPenjualan()}
              className="btn btn-warning"
              disabled={this.state.isLoadingBatal}
            >
              {this.state.isLoadingBatal ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Batal
                </>
              ) : (
                "Batal"
              )}
            </button>
          </div>
        </div>
        {/* {this.state.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null} */}
      </form>
    );
  }
}

HeadBatalPenjualan = reduxForm({
  form: "HeadBatalPenjualan",
  enableReinitialize: true,
  validate: ValidasiBaralJual,
})(HeadBatalPenjualan);
export default connect(maptostate, null)(HeadBatalPenjualan);
